const ResourcesListReducer = (state, action) => {
  switch (action.type) {
    case "FETCH_SUCCESS_RESOURCES_LIST_DATA":
      console.log("i am righ", action)
      return {
        loading: false,
        data: ["hello"],
        error: "",
      };

    case "FETCH_ERROR_RESOURCES_LIST_DATA":
      return {
        loading: false,
        data: {},
        error: "Error in fetching resources",
      };

    default:
      return state;
  }
};

export { ResourcesListReducer };
