import React, { useState, useEffect, useReducer, useContext } from "react";
import "./App.css";
import Route from "./routes/index";
import GeoModal from "./components/home/geoLocationModal/GeoModal";
import firebase from "firebase/app";
import { firebaseConfig } from "./firebase.config";
import { InitialResourcesListState } from "./contexts/store";
import { ResourcesListReducer } from "./contexts/reducer";
import { Util } from "./utils";

export const ResourcesListData = React.createContext();

function App() {
  const [state, setState] = useState({
    fetchedUserLocation: false,
    lat: "",
    lng: "",
    address: "",
    distance: "50000",
    perPage: "5",
    page: "1",
    sortBy: "createdAt",
    sortOrder: "asc",
  });
  const [resourcesListState, dispatchResourceList] = useReducer(
    ResourcesListReducer,
    InitialResourcesListState
  );

  const enableRedirect = (stateObj) => {
    setState((prevState) => ({
      ...prevState,
      fetchedUserLocation: stateObj.fetchedUserLocation,
      lat: stateObj.lat,
      lng: stateObj.lng,
      address: stateObj.address,
    }));

    var requestOptions = {
      method: "GET",
      redirect: "follow",
    };
    fetch(
      // `${Util.endpoints.getResourcesList}?page=${state.page}&perPage=${state.perPage}&sortBy=${state.sortBy}&sortOrder=${state.sortOrder}`,
      `${Util.endpoints.getResourcesListNearMe}?page=${state.page}&perPage=${state.perPage}&lat=${stateObj.lat}&lng=${stateObj.lng}&distance=${state.distance}`,
      requestOptions
    )
      .then((response) => {
        console.log("response", response);
        return response.json();
      })
      .then((res) => {
        console.log("boom", res)
        dispatchResourceList({
          type: "FETCH_SUCCESS_RESOURCES_LIST_DATA",
          payload: res,
        });
      })
      .catch((error) => {
        dispatchResourceList({ type: "FETCH_ERROR_RESOURCES_LIST_DATA" });
      });
  };

  if (!firebase.apps.length) {
    firebase.initializeApp(firebaseConfig);
  } else {
    firebase.app();
  }

  return (
    <>
      {state.fetchedUserLocation ? (
        <ResourcesListData.Provider resourcesListState={resourcesListState}>
          <Route />
        </ResourcesListData.Provider>
      ) : (
        <GeoModal enableRedirect={enableRedirect} />
      )}
    </>
  );
}

export default App;
